.profileContainer {
  display: flex;
  justify-content: center;
  .paper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    width: 415px;
    margin-top: 100px;
    .title {
      color: #000000;
      font-size: 22px;
      font-style: normal;
      font-weight: 600;
      line-height: 27px;
      text-transform: capitalize;
    }
    .avatarContainer {
      position: relative;
      margin: 30px auto 0;
      .avatarImg {
        border-radius: 100%;
        height: 100px;
        width: 100px;
      }
      .inputBlock {
        position: relative;
        width: 350px;
        .input {
          min-height: 100%;
          width: 100%;
          outline: none;
          border: none;
          border-bottom: 2px solid rgba(0, 0, 0, 0.2);
          color: #000000;
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: 24px;
          margin: 0 10px 0 0;
          padding: 0;
        }
        .label {
          color: #000000;
          display: flex;
          justify-content: flex-start;
          font-size: 13px;
          font-style: normal;
          font-weight: 400;
          opacity: 0.5;
          text-transform: lowercase;
        }
        .inputButton {
          bottom: 10px;
          right: 0;
          max-height: 25px;
          position: absolute;
          width: 50px;
        
          font-size: 12px;
          font-style: normal;
          font-weight: 400;
          line-height: 24px;
          text-transform: uppercase;
        }
      }
      .camera {
        cursor: pointer;
        position: absolute;
        bottom: 0;
        right: 0;
        background: #808080;
        border: 1px solid #FFFFFF;
        border-radius: 100%;
        height: 32px;
        width: 32px;
      }
    }
    .span {
      color: #000000;
      cursor: pointer;
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: 24px;
      margin: 0;
      padding: 0;
    }
    .info {
      color: #000000;
      display: flex;
      justify-content: flex-start;
      font-size: 13px;
      font-style: normal;
      font-weight: 400;
      opacity: 0.5;
      text-transform: lowercase;
    }
    .errorInfo {
      color: red;
      display: flex;
      justify-content: flex-start;
      font-size: 13px;
      font-style: normal;
      font-weight: 400;
      opacity: 0.5;
      text-transform: lowercase;
    }
    .email {
      color: #000000;
      display: flex;
      justify-content: center;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
      margin-top: 15px;
      opacity: 0.5;
    }
    .logautButton {
      display: flex;
      gap: 5px;
      background-color: #FCFCFC;
      border-radius: 30px;
      box-shadow: 0px 2px 10px rgba(109, 109, 109, 0.25), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
      height: 35px;
      margin-top: 15px;
      width: 125px;
    
      color: #000000;
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
      letter-spacing: 0.01em;  
      opacity: 0.8;
      text-align: center;
      text-transform: none;
    }
  }
}